// lib/api.js

// ==============================
// Config
// ==============================
const BASE = (process.env.NEXT_PUBLIC_API_BASE_URL || "http://localhost:4000").trim();

// ==============================
// Internal state (registered by AuthProvider)
// ==============================
let _getAccessToken = null;
let _onUnauthorized = null;
// à¹ƒà¸Šà¹‰à¸à¸±à¸™ â€œstampedeâ€ à¸•à¸­à¸™ refresh token à¹ƒà¸«à¹‰ run à¸„à¸£à¸±à¹‰à¸‡à¹€à¸”à¸µà¸¢à¸§à¹à¸¥à¹‰à¸§ await à¸£à¹ˆà¸§à¸¡à¸à¸±à¸™
let _refreshPromise = null;

// ==============================
// Small utilities
// ==============================
function isAbsoluteUrl(u) {
  return /^https?:\/\//i.test(String(u || ""));
}

function trimTrailingSlash(s) {
  return String(s || "").replace(/\/+$/g, "");
}
function trimLeadingSlash(s) {
  return String(s || "").replace(/^\/+/g, "");
}

/**
 * à¸£à¸§à¸¡ BASE + path à¹ƒà¸«à¹‰à¹€à¸›à¹‡à¸™ URL à¸ªà¸³à¸«à¸£à¸±à¸š â€œà¹à¸ªà¸”à¸‡à¸œà¸¥â€ (à¹€à¸Šà¹ˆà¸™ src/href) à¹„à¸¡à¹ˆà¸¢à¸´à¸‡ request à¹€à¸­à¸‡
 * - à¸£à¸­à¸‡à¸£à¸±à¸šà¸—à¸±à¹‰à¸‡ path (`/api/...` à¸«à¸£à¸·à¸­ `api/...`) à¹à¸¥à¸° absolute URL (à¸‚à¹‰à¸²à¸¡ BASE)
 * - à¸›à¸£à¸±à¸š slash à¸­à¸±à¸•à¹‚à¸™à¸¡à¸±à¸•à¸´ à¹„à¸¡à¹ˆà¹ƒà¸«à¹‰à¸‹à¹‰à¸³à¸à¸±à¸™
 */
export function apiURL(pathOrUrl = "") {
  const u = String(pathOrUrl || "");
  if (!u) return trimTrailingSlash(BASE); // à¸à¸£à¸“à¸µà¸­à¸¢à¸²à¸à¹„à¸”à¹‰ BASE à¹€à¸›à¸¥à¹ˆà¸² à¹†
  if (isAbsoluteUrl(u)) return u;

  const base = trimTrailingSlash(BASE);
  const path = "/" + trimLeadingSlash(u); // à¸šà¸±à¸‡à¸„à¸±à¸šà¹ƒà¸«à¹‰à¸‚à¸¶à¹‰à¸™à¸•à¹‰à¸™à¸”à¹‰à¸§à¸¢ /
  return base + path;
}

/** à¹à¸›à¸¥à¸‡ response -> data (auto json/text) */
export async function parseResponse(res) {
  const ct = res.headers.get("content-type") || "";
  const data = ct.includes("application/json") ? await res.json() : await res.text();
  return data;
}

/** à¸•à¸£à¸§à¸ˆà¸§à¹ˆà¸²à¹€à¸›à¹‡à¸™ URL refresh token (à¸à¸±à¸™à¸§à¸™à¸¥à¸¹à¸› onUnauthorized) */
function isAuthRefreshUrl(url) {
  try {
    const u = new URL(url, BASE);
    // à¸•à¸£à¸‡à¸à¸±à¸š backend path: /api/auth/refresh
    return /\/api\/auth\/refresh($|\?)/i.test(u.pathname);
  } catch {
    return false;
  }
}

// ==============================
// Public API
// ==============================

/**
 * à¹ƒà¸«à¹‰ AuthProvider à¹€à¸£à¸µà¸¢à¸à¸•à¸­à¸™à¸šà¸¹à¸•à¹à¸­à¸› à¹€à¸žà¸·à¹ˆà¸­à¸šà¸­à¸à¸§à¹ˆà¸²à¸ˆà¸°à¹€à¸­à¸² accessToken à¸ˆà¸²à¸à¹„à¸«à¸™
 * à¹à¸¥à¸°à¸–à¹‰à¸²à¹€à¸ˆà¸­ 401 à¹ƒà¸«à¹‰à¸—à¸³à¸­à¸°à¹„à¸£ (à¹€à¸Šà¹ˆà¸™ à¹€à¸£à¸µà¸¢à¸ refresh)
 */
export function configureApiAuth({ getAccessToken, onUnauthorized } = {}) {
  _getAccessToken = typeof getAccessToken === "function" ? getAccessToken : null;
  _onUnauthorized = typeof onUnauthorized === "function" ? onUnauthorized : null;
}

/**
 * Wrapper à¸‚à¸­à¸‡ fetch à¸ªà¸³à¸«à¸£à¸±à¸šà¸„à¸¸à¸¢à¸à¸±à¸š backend
 * à¸‚à¹‰à¸­à¸”à¸µ:
 *  - à¸£à¸±à¸šà¸—à¸±à¹‰à¸‡ absolute URL à¹à¸¥à¸° path (à¹à¸™à¸°à¸™à¸³ path `/api/...`)
 *  - à¹€à¸•à¸´à¸¡ BASE à¹ƒà¸«à¹‰à¸­à¸±à¸•à¹‚à¸™à¸¡à¸±à¸•à¸´à¹€à¸¡à¸·à¹ˆà¸­à¹€à¸›à¹‡à¸™ path
 *  - à¹à¸™à¸š Authorization Bearer à¹ƒà¸«à¹‰à¸­à¸±à¸•à¹‚à¸™à¸¡à¸±à¸•à¸´ (à¸–à¹‰à¸²à¸¡à¸µ configureApiAuth)
 *  - à¸ˆà¸±à¸”à¸à¸²à¸£ 401 -> à¹€à¸£à¸µà¸¢à¸ onUnauthorized() (à¹€à¸Šà¹ˆà¸™ refresh) + retry à¹ƒà¸«à¹‰à¸­à¸±à¸•à¹‚à¸™à¸¡à¸±à¸•à¸´ 1 à¸„à¸£à¸±à¹‰à¸‡
 *  - à¸ªà¹ˆà¸‡ cookie à¹€à¸ªà¸¡à¸­ (credentials: "include")
 *  - à¸–à¹‰à¸² body à¹€à¸›à¹‡à¸™ object (à¹„à¸¡à¹ˆà¹ƒà¸Šà¹ˆ FormData) à¸ˆà¸°à¹ƒà¸ªà¹ˆ content-type: application/json à¹à¸¥à¸° stringify à¹ƒà¸«à¹‰
 *
 * @param {string} pathOrUrl - "/api/..." à¸«à¸£à¸·à¸­ absolute URL
 * @param {RequestInit & { body?: any }} init
 * @param {{ getAccessToken?: ()=>string, onUnauthorized?: ()=>Promise<boolean>, retryOnce?: boolean }} opts
 */
export async function apiFetch(pathOrUrl, init = {}, opts = {}) {
  // 1) à¸ªà¸£à¹‰à¸²à¸‡ URL
  const url = isAbsoluteUrl(pathOrUrl) ? String(pathOrUrl) : apiURL(pathOrUrl);

  // 2) à¹€à¸•à¸£à¸µà¸¢à¸¡ header + body
  const isForm = typeof FormData !== "undefined" && init?.body instanceof FormData;
  const headers = new Headers(init?.headers || {});
  if (!isForm && init?.body && typeof init.body === "object" && !headers.has("content-type")) {
    headers.set("content-type", "application/json");
  }

  // 3) à¹€à¸¥à¸·à¸­à¸ token getter / unauthorized handler (à¸ªà¸²à¸¡à¸²à¸£à¸– override à¸£à¸²à¸¢à¸„à¸³à¸‚à¸­à¹„à¸”à¹‰)
  const getAccessToken =
    typeof opts.getAccessToken === "function" ? opts.getAccessToken : _getAccessToken;
  const onUnauthorized =
    typeof opts.onUnauthorized === "function" ? opts.onUnauthorized : _onUnauthorized;

  // 4) à¹à¸™à¸š Bearer à¸–à¹‰à¸²à¸¡à¸µ
  if (typeof getAccessToken === "function") {
    const token = getAccessToken();
    if (token && !headers.has("authorization")) {
      headers.set("authorization", `Bearer ${token}`);
    }
  }

  const requestInit = {
    method: init?.method || "GET",
    credentials: "include",
    cache: "no-store",
    ...init,
    headers,
    body: isForm
      ? init.body
      : init?.body && typeof init.body === "object"
      ? JSON.stringify(init.body)
      : init?.body,
  };

  // 5) à¸¢à¸´à¸‡à¸„à¸£à¸±à¹‰à¸‡à¸—à¸µà¹ˆ 1
  const allowRefresh = !isAuthRefreshUrl(url);
  const retryOnce = opts.retryOnce !== false; // default = true
  let res = await fetch(url, requestInit);

  // 6) à¸–à¹‰à¸² 401 à¹à¸¥à¸°à¸­à¸™à¸¸à¸à¸²à¸• refresh -> à¹€à¸£à¸µà¸¢à¸ onUnauthorized() à¹à¸šà¸šà¸£à¸§à¸¡à¸¨à¸¹à¸™à¸¢à¹Œ
  if (res.status === 401 && allowRefresh && typeof onUnauthorized === "function") {
    if (!_refreshPromise) {
      _refreshPromise = (async () => {
        try {
          const ok = await onUnauthorized();
          return !!ok;
        } finally {
          // à¸›à¸¥à¹ˆà¸­à¸¢à¹ƒà¸«à¹‰à¸„à¸³à¸‚à¸­à¸­à¸·à¹ˆà¸™ à¹† à¹„à¸”à¹‰ await à¸à¹ˆà¸­à¸™ à¹à¸¥à¹‰à¸§à¸„à¹ˆà¸­à¸¢à¸¥à¹‰à¸²à¸‡à¸•à¸±à¸§à¹à¸›à¸£
          setTimeout(() => {
            _refreshPromise = null;
          }, 0);
        }
      })();
    }

    const ok = await _refreshPromise;

    // 7) à¸–à¹‰à¸² refresh à¸ªà¸³à¹€à¸£à¹‡à¸ˆ à¹à¸¥à¸°à¸­à¸™à¸¸à¸à¸²à¸• retry
    if (ok && retryOnce) {
      // à¹à¸™à¸š token à¸¥à¹ˆà¸²à¸ªà¸¸à¸”à¸à¹ˆà¸­à¸™à¸¢à¸´à¸‡à¸‹à¹‰à¸³
      if (typeof getAccessToken === "function") {
        const t2 = getAccessToken();
        if (t2) requestInit.headers.set("authorization", `Bearer ${t2}`);
      }
      res = await fetch(url, requestInit);
    }
  }

  // 8) parse + throw à¸–à¹‰à¸²à¹„à¸¡à¹ˆ ok
  const data = await parseResponse(res);
  if (!res.ok) {
    const msg =
      (data && (data.error || data.message)) || (typeof data === "string" ? data : "Request failed");
    const err = new Error(msg);
    err.status = res.status;
    err.data = data;
    throw err;
  }

  return data;
}

